"use client";

import React from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { TokenUsageWidget } from "@/components/subscription/TokenUsageWidget";
import { ChannelConfiguration } from "@/components/subscription/ChannelConfiguration";
import { UpgradeModal } from "@/components/subscription/UpgradeModal";
import { useSubscription } from "@/contexts/SubscriptionContext";
import { useTokenHandling } from "@/hooks/useTokenHandling";
import { Button } from "@/components/ui/button";
import { Settings, Zap, AlertTriangle } from "lucide-react";

export default function SubscriptionPage() {
  const { subscription, plans } = useSubscription();
  const { showUpgradeModal, tokenError, closeUpgradeModal } =
    useTokenHandling();

  const getCurrentPlan = () => {
    if (!subscription || !plans) return null;

    const planEntries = Object.entries(plans);
    const currentPlan = planEntries.find(
      ([, plan]) => plan.monthly_token_limit === subscription.monthly_limit
    );

    return currentPlan ? { key: currentPlan[0], plan: currentPlan[1] } : null;
  };

  const currentPlan = getCurrentPlan();
  const isNearLimit = subscription
    ? subscription.usage_percentage >= 80
    : false;

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold">Subscription Management</h1>
          <p className="text-gray-600 mt-1">
            Manage your subscription, monitor usage, and configure channels
          </p>
        </div>
        {isNearLimit && (
          <Button variant="destructive" className="flex items-center space-x-2">
            <AlertTriangle className="w-4 h-4" />
            <span>Upgrade Plan</span>
          </Button>
        )}
      </div>

      {/* Current Plan Overview */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center space-x-2">
            <Zap className="w-5 h-5" />
            <span>Current Plan</span>
          </CardTitle>
        </CardHeader>
        <CardContent>
          {currentPlan ? (
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div>
                <h3 className="text-xl font-semibold">
                  {currentPlan.plan.name}
                </h3>
                <p className="text-gray-600">
                  ${currentPlan.plan.price_per_month}/month
                </p>
              </div>
              <div>
                <p className="text-sm text-gray-600">Monthly Token Limit</p>
                <p className="text-lg font-semibold">
                  {new Intl.NumberFormat().format(
                    currentPlan.plan.monthly_token_limit
                  )}
                </p>
              </div>
              <div>
                <p className="text-sm text-gray-600">Max Chatbots</p>
                <p className="text-lg font-semibold">
                  {currentPlan.plan.max_chatbots}
                </p>
              </div>
            </div>
          ) : (
            <p className="text-gray-600">No subscription plan found</p>
          )}
        </CardContent>
      </Card>

      {/* Token Usage Widget */}
      <TokenUsageWidget />

      {/* Main Content Tabs */}
      <Tabs defaultValue="channels" className="space-y-4">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="channels" className="flex items-center space-x-2">
            <Settings className="w-4 h-4" />
            <span>Channels</span>
          </TabsTrigger>
          <TabsTrigger value="settings" className="flex items-center space-x-2">
            <Settings className="w-4 h-4" />
            <span>Settings</span>
          </TabsTrigger>
        </TabsList>

        <TabsContent value="channels">
          <ChannelConfiguration />
        </TabsContent>

        <TabsContent value="settings">
          <Card>
            <CardHeader>
              <CardTitle>Subscription Settings</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div className="text-center py-8">
                <Settings className="w-12 h-12 text-gray-400 mx-auto mb-4" />
                <h3 className="text-lg font-semibold mb-2">
                  Settings Coming Soon
                </h3>
                <p className="text-gray-600">
                  Advanced subscription settings will be available here.
                </p>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* Upgrade Modal */}
      <UpgradeModal
        isOpen={showUpgradeModal}
        onClose={closeUpgradeModal}
        currentTokensUsed={subscription?.tokens_used_this_month}
        currentTokensLimit={subscription?.monthly_limit}
        tokensRequired={tokenError?.tokens_requested}
      />
    </div>
  );
}
