"use client";

import React, { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Switch } from "@/components/ui/switch";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import {
  Settings,
  Save,
  RefreshCw,
  AlertCircle,
  CheckCircle,
  Loader2,
  Globe,
  MessageSquare,
  Smartphone,
  Camera,
  Code,
  Monitor,
} from "lucide-react";
import { subscriptionApi } from "@/app/api/subscription";
import { useSubscription } from "@/contexts/SubscriptionContext";
import { toast } from "sonner";
import type {
  SupportedChannels,
  ChannelConfigurations,
  ChannelConfigUpdateRequest,
} from "@/types/subscription";

interface ChannelConfigurationProps {
  className?: string;
}

const CHANNEL_ICONS = {
  website: Globe,
  whatsapp: Smartphone,
  messenger: MessageSquare,
  instagram: Camera,
  api: Code,
  mobile_app: Monitor,
};

export const ChannelConfiguration: React.FC<ChannelConfigurationProps> = ({
  className,
}) => {
  const { subscription } = useSubscription();
  const [supportedChannels, setSupportedChannels] =
    useState<SupportedChannels | null>(null);
  const [channelConfigs, setChannelConfigs] =
    useState<ChannelConfigurations | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isSaving, setIsSaving] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState<string>("website");

  useEffect(() => {
    const loadChannelData = async () => {
      try {
        setIsLoading(true);
        setError(null);

        const [channels, configs] = await Promise.all([
          subscriptionApi.getSupportedChannels(),
          subscription?.subscription_id
            ? subscriptionApi.getChannelConfigurations(
                subscription.subscription_id
              )
            : null,
        ]);

        setSupportedChannels(channels);
        setChannelConfigs(configs);

        // Set first channel as active tab
        if (channels && Object.keys(channels).length > 0) {
          setActiveTab(Object.keys(channels)[0]);
        }
      } catch (err) {
        console.error("Failed to load channel data:", err);
        setError("Failed to load channel configurations");
      } finally {
        setIsLoading(false);
      }
    };

    loadChannelData();
  }, [subscription?.subscription_id]);

  const handleConfigUpdate = async (
    channel: string,
    updates: ChannelConfigUpdateRequest
  ) => {
    if (!subscription?.subscription_id) return;

    try {
      setIsSaving(true);

      const result = await subscriptionApi.updateChannelConfiguration(
        subscription.subscription_id,
        channel,
        updates
      );

      toast.success(`Channel configuration updated for ${channel}`);

      // Refresh configurations
      const updatedConfigs = await subscriptionApi.getChannelConfigurations(
        subscription.subscription_id
      );
      setChannelConfigs(updatedConfigs);
    } catch (err) {
      console.error("Failed to update channel configuration:", err);
      toast.error(`Failed to update ${channel} configuration`);
    } finally {
      setIsSaving(false);
    }
  };

  const getChannelIcon = (channel: string) => {
    const IconComponent =
      CHANNEL_ICONS[channel as keyof typeof CHANNEL_ICONS] || Settings;
    return <IconComponent className="w-5 h-5" />;
  };

  const formatNumber = (num: number) => {
    return new Intl.NumberFormat().format(num);
  };

  if (isLoading) {
    return (
      <Card className={className}>
        <CardContent className="p-6">
          <div className="flex items-center justify-center">
            <Loader2 className="w-8 h-8 animate-spin text-blue-500" />
            <span className="ml-2 text-gray-600">
              Loading channel configurations...
            </span>
          </div>
        </CardContent>
      </Card>
    );
  }

  if (error || !supportedChannels) {
    return (
      <Card className={className}>
        <CardContent className="p-6">
          <div className="text-center">
            <AlertCircle className="w-12 h-12 text-red-500 mx-auto mb-4" />
            <h3 className="text-lg font-semibold mb-2">
              Configuration Unavailable
            </h3>
            <p className="text-gray-600 mb-4">
              {error || "No channel configurations available"}
            </p>
            <Button onClick={() => window.location.reload()}>Try Again</Button>
          </div>
        </CardContent>
      </Card>
    );
  }

  const channelEntries = Object.entries(supportedChannels);

  return (
    <div className={className}>
      <div className="mb-6">
        <h2 className="text-2xl font-bold mb-2">Channel Configuration</h2>
        <p className="text-gray-600">
          Configure settings for each communication channel in your
          subscription.
        </p>
      </div>

      <Tabs
        value={activeTab}
        onValueChange={setActiveTab}
        className="space-y-4"
      >
        <TabsList className="grid w-full grid-cols-2 md:grid-cols-3 lg:grid-cols-6">
          {channelEntries.map(([channelKey, channelInfo]) => (
            <TabsTrigger
              key={channelKey}
              value={channelKey}
              className="flex items-center space-x-2"
            >
              {getChannelIcon(channelKey)}
              <span className="hidden sm:inline capitalize">{channelKey}</span>
            </TabsTrigger>
          ))}
        </TabsList>

        {channelEntries.map(([channelKey, channelInfo]) => {
          const config = channelConfigs?.[channelKey];
          const isEnabled = config?.enabled ?? false;

          return (
            <TabsContent
              key={channelKey}
              value={channelKey}
              className="space-y-4"
            >
              <Card>
                <CardHeader>
                  <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-3">
                      {getChannelIcon(channelKey)}
                      <div>
                        <CardTitle className="capitalize">
                          {channelKey} Configuration
                        </CardTitle>
                        <p className="text-sm text-gray-600">
                          {channelInfo.description}
                        </p>
                      </div>
                    </div>
                    <Badge variant={isEnabled ? "default" : "secondary"}>
                      {isEnabled ? "Enabled" : "Disabled"}
                    </Badge>
                  </div>
                </CardHeader>
                <CardContent className="space-y-6">
                  {/* Channel Information */}
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4 p-4 bg-gray-50 rounded-lg">
                    <div>
                      <h4 className="font-semibold mb-2">Channel Details</h4>
                      <div className="space-y-1 text-sm">
                        <p>
                          <span className="font-medium">Name:</span>{" "}
                          {channelInfo.name}
                        </p>
                        <p>
                          <span className="font-medium">Description:</span>{" "}
                          {channelInfo.description}
                        </p>
                        <p>
                          <span className="font-medium">Icon:</span>{" "}
                          {channelInfo.icon}
                        </p>
                      </div>
                    </div>
                    <div>
                      <h4 className="font-semibold mb-2">Typical Use Cases</h4>
                      <ul className="text-sm space-y-1">
                        {channelInfo.typical_use_cases.map((useCase, index) => (
                          <li key={index} className="flex items-center">
                            <CheckCircle className="w-3 h-3 text-green-500 mr-2" />
                            {useCase}
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>

                  {/* Configuration Form */}
                  <div className="space-y-4">
                    <h4 className="font-semibold">Configuration Settings</h4>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      {/* Enable/Disable */}
                      <div className="flex items-center justify-between p-3 border rounded-lg">
                        <div>
                          <Label
                            htmlFor={`${channelKey}-enabled`}
                            className="font-medium"
                          >
                            Enable Channel
                          </Label>
                          <p className="text-sm text-gray-600">
                            Allow this channel to receive messages
                          </p>
                        </div>
                        <Switch
                          id={`${channelKey}-enabled`}
                          checked={isEnabled}
                          onCheckedChange={(checked) =>
                            handleConfigUpdate(channelKey, { enabled: checked })
                          }
                          disabled={isSaving}
                        />
                      </div>

                      {/* Rate Limit */}
                      <div className="space-y-2">
                        <Label htmlFor={`${channelKey}-rate-limit`}>
                          Rate Limit (messages/minute)
                        </Label>
                        <Input
                          id={`${channelKey}-rate-limit`}
                          type="number"
                          value={config?.rate_limit_per_minute || 60}
                          onChange={(e) =>
                            handleConfigUpdate(channelKey, {
                              rate_limit_per_minute: parseInt(e.target.value),
                            })
                          }
                          disabled={isSaving}
                          min="1"
                          max="1000"
                        />
                      </div>

                      {/* Webhook URL */}
                      <div className="space-y-2 md:col-span-2">
                        <Label htmlFor={`${channelKey}-webhook`}>
                          Webhook URL
                        </Label>
                        <Input
                          id={`${channelKey}-webhook`}
                          type="url"
                          value={config?.webhook_url || ""}
                          onChange={(e) =>
                            handleConfigUpdate(channelKey, {
                              webhook_url: e.target.value,
                            })
                          }
                          disabled={isSaving}
                          placeholder="https://your-domain.com/webhook"
                        />
                        <p className="text-sm text-gray-600">
                          URL to receive webhook notifications for this channel
                        </p>
                      </div>
                    </div>

                    {/* Custom Settings */}
                    <div className="space-y-2">
                      <Label>Custom Settings</Label>
                      <div className="p-3 border rounded-lg bg-gray-50">
                        <pre className="text-sm text-gray-600">
                          {JSON.stringify(
                            config?.custom_settings || {},
                            null,
                            2
                          )}
                        </pre>
                        <p className="text-xs text-gray-500 mt-2">
                          Custom settings are managed by the backend API
                        </p>
                      </div>
                    </div>

                    {/* Configuration Status */}
                    <div className="flex items-center justify-between p-3 border rounded-lg">
                      <div>
                        <p className="font-medium">Configuration Status</p>
                        <p className="text-sm text-gray-600">
                          Last updated:{" "}
                          {config?.updated_at
                            ? new Date(config.updated_at).toLocaleString()
                            : "Never"}
                        </p>
                      </div>
                      <div className="flex items-center space-x-2">
                        {isEnabled ? (
                          <CheckCircle className="w-5 h-5 text-green-500" />
                        ) : (
                          <AlertCircle className="w-5 h-5 text-yellow-500" />
                        )}
                        <span className="text-sm font-medium">
                          {isEnabled ? "Active" : "Inactive"}
                        </span>
                      </div>
                    </div>
                  </div>

                  {/* Action Buttons */}
                  <div className="flex justify-end space-x-2 pt-4 border-t">
                    <Button
                      variant="outline"
                      onClick={() => window.location.reload()}
                      disabled={isSaving}
                    >
                      <RefreshCw className="w-4 h-4 mr-2" />
                      Refresh
                    </Button>
                    <Button
                      onClick={() => handleConfigUpdate(channelKey, {})}
                      disabled={isSaving}
                    >
                      {isSaving ? (
                        <>
                          <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                          Saving...
                        </>
                      ) : (
                        <>
                          <Save className="w-4 h-4 mr-2" />
                          Save Changes
                        </>
                      )}
                    </Button>
                  </div>
                </CardContent>
              </Card>
            </TabsContent>
          );
        })}
      </Tabs>

      {/* Summary */}
      <Card className="mt-6">
        <CardHeader>
          <CardTitle>Configuration Summary</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {channelEntries.map(([channelKey, channelInfo]) => {
              const config = channelConfigs?.[channelKey];
              const isEnabled = config?.enabled ?? false;

              return (
                <div
                  key={channelKey}
                  className="flex items-center justify-between p-3 border rounded-lg"
                >
                  <div className="flex items-center space-x-2">
                    {getChannelIcon(channelKey)}
                    <span className="capitalize font-medium">{channelKey}</span>
                  </div>
                  <Badge variant={isEnabled ? "default" : "secondary"}>
                    {isEnabled ? "Enabled" : "Disabled"}
                  </Badge>
                </div>
              );
            })}
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default ChannelConfiguration;
