"use client";

import React, { useState } from "react";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Check, AlertTriangle, Zap, Crown, Building } from "lucide-react";
import { useSubscription } from "@/contexts/SubscriptionContext";
import { subscriptionApi } from "@/app/api/subscription";
import { toast } from "sonner";
import type { SubscriptionPlans } from "@/types/subscription";

interface UpgradeModalProps {
  isOpen: boolean;
  onClose: () => void;
  currentTokensUsed?: number;
  currentTokensLimit?: number;
  tokensRequired?: number;
}

export const UpgradeModal: React.FC<UpgradeModalProps> = ({
  isOpen,
  onClose,
  currentTokensUsed = 0,
  currentTokensLimit = 0,
  tokensRequired = 0,
}) => {
  const { plans, refreshSubscription } = useSubscription();
  const [isLoading, setIsLoading] = useState(false);
  const [selectedPlan, setSelectedPlan] = useState<string | null>(null);

  if (!plans) {
    return null;
  }

  const planEntries = Object.entries(plans);
  const currentPlan = planEntries.find(
    ([_, plan]) => plan.monthly_token_limit === currentTokensLimit
  );
  const upgradeOptions = planEntries.filter(
    ([_, plan]) => plan.monthly_token_limit > currentTokensLimit
  );

  const getPlanIcon = (planKey: string) => {
    switch (planKey) {
      case "basic":
        return <Zap className="w-5 h-5 text-blue-500" />;
      case "professional":
        return <Crown className="w-5 h-5 text-purple-500" />;
      case "enterprise":
        return <Building className="w-5 h-5 text-green-500" />;
      default:
        return <Zap className="w-5 h-5 text-gray-500" />;
    }
  };

  const getPlanColor = (planKey: string) => {
    switch (planKey) {
      case "basic":
        return "border-blue-200 hover:border-blue-300";
      case "professional":
        return "border-purple-200 hover:border-purple-300";
      case "enterprise":
        return "border-green-200 hover:border-green-300";
      default:
        return "border-gray-200 hover:border-gray-300";
    }
  };

  const handleUpgrade = async (planKey: string) => {
    setIsLoading(true);
    try {
      // This would typically redirect to a payment processor
      // For now, we'll show a success message
      toast.success(
        `Upgrade to ${
          plans[planKey as keyof SubscriptionPlans].name
        } initiated!`
      );

      // Refresh subscription status
      await refreshSubscription();

      onClose();
    } catch (error) {
      console.error("Upgrade failed:", error);
      toast.error("Upgrade failed. Please try again.");
    } finally {
      setIsLoading(false);
    }
  };

  const formatNumber = (num: number) => {
    return new Intl.NumberFormat().format(num);
  };

  const getUsagePercentage = () => {
    if (currentTokensLimit === 0) return 0;
    return (currentTokensUsed / currentTokensLimit) * 100;
  };

  const isNearLimit = getUsagePercentage() >= 80;
  const isAtLimit = getUsagePercentage() >= 95;

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle className="flex items-center space-x-2">
            <AlertTriangle className="w-6 h-6 text-yellow-500" />
            <span>Upgrade Your Plan</span>
          </DialogTitle>
          <DialogDescription>
            {isAtLimit
              ? "You've reached your token limit. Upgrade to continue using the service."
              : "You're approaching your token limit. Upgrade now to avoid interruption."}
          </DialogDescription>
        </DialogHeader>

        <div className="space-y-6">
          {/* Current Usage Status */}
          <Card className="bg-yellow-50 border-yellow-200">
            <CardContent className="p-4">
              <div className="flex items-center justify-between">
                <div>
                  <h3 className="font-semibold text-yellow-800">
                    Current Usage
                  </h3>
                  <p className="text-sm text-yellow-700">
                    {formatNumber(currentTokensUsed)} /{" "}
                    {formatNumber(currentTokensLimit)} tokens used
                  </p>
                </div>
                <Badge
                  variant="secondary"
                  className="bg-yellow-100 text-yellow-800"
                >
                  {getUsagePercentage().toFixed(1)}% Used
                </Badge>
              </div>
              {tokensRequired > 0 && (
                <div className="mt-2 text-sm text-yellow-700">
                  <strong>Tokens needed:</strong> {formatNumber(tokensRequired)}
                </div>
              )}
            </CardContent>
          </Card>

          {/* Current Plan */}
          {currentPlan && (
            <div>
              <h3 className="text-lg font-semibold mb-3">Current Plan</h3>
              <Card className="bg-gray-50 border-gray-200">
                <CardContent className="p-4">
                  <div className="flex items-center space-x-3">
                    {getPlanIcon(currentPlan[0])}
                    <div>
                      <h4 className="font-semibold">{currentPlan[1].name}</h4>
                      <p className="text-sm text-gray-600">
                        {formatNumber(currentPlan[1].monthly_token_limit)}{" "}
                        tokens/month
                      </p>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </div>
          )}

          {/* Upgrade Options */}
          <div>
            <h3 className="text-lg font-semibold mb-3">Upgrade Options</h3>
            <div className="grid md:grid-cols-2 gap-4">
              {upgradeOptions.map(([planKey, plan]) => (
                <Card
                  key={planKey}
                  className={`cursor-pointer transition-all ${getPlanColor(
                    planKey
                  )} ${selectedPlan === planKey ? "ring-2 ring-blue-500" : ""}`}
                  onClick={() => setSelectedPlan(planKey)}
                >
                  <CardHeader className="pb-3">
                    <div className="flex items-center justify-between">
                      <div className="flex items-center space-x-2">
                        {getPlanIcon(planKey)}
                        <CardTitle className="text-lg">{plan.name}</CardTitle>
                      </div>
                      <Badge variant="outline">Upgrade</Badge>
                    </div>
                  </CardHeader>
                  <CardContent className="space-y-3">
                    <div className="text-2xl font-bold">
                      ${plan.price_per_month}
                      <span className="text-sm font-normal text-gray-500">
                        /month
                      </span>
                    </div>

                    <div className="space-y-2">
                      <div className="flex items-center space-x-2">
                        <Check className="w-4 h-4 text-green-500" />
                        <span className="text-sm">
                          {formatNumber(plan.monthly_token_limit)} tokens/month
                        </span>
                      </div>
                      <div className="flex items-center space-x-2">
                        <Check className="w-4 h-4 text-green-500" />
                        <span className="text-sm">
                          {plan.max_chatbots} chatbots
                        </span>
                      </div>
                      <div className="flex items-center space-x-2">
                        <Check className="w-4 h-4 text-green-500" />
                        <span className="text-sm">
                          {plan.max_documents_per_chatbot} documents per chatbot
                        </span>
                      </div>
                      {plan.priority_support && (
                        <div className="flex items-center space-x-2">
                          <Check className="w-4 h-4 text-green-500" />
                          <span className="text-sm">Priority support</span>
                        </div>
                      )}
                      {plan.custom_branding && (
                        <div className="flex items-center space-x-2">
                          <Check className="w-4 h-4 text-green-500" />
                          <span className="text-sm">Custom branding</span>
                        </div>
                      )}
                      {plan.api_access && (
                        <div className="flex items-center space-x-2">
                          <Check className="w-4 h-4 text-green-500" />
                          <span className="text-sm">API access</span>
                        </div>
                      )}
                    </div>

                    <Button
                      className="w-full"
                      onClick={() => handleUpgrade(planKey)}
                      disabled={isLoading}
                    >
                      {isLoading ? "Processing..." : `Upgrade to ${plan.name}`}
                    </Button>
                  </CardContent>
                </Card>
              ))}
            </div>
          </div>

          {/* Benefits of Upgrading */}
          <Card className="bg-blue-50 border-blue-200">
            <CardContent className="p-4">
              <h3 className="font-semibold text-blue-800 mb-2">
                Benefits of Upgrading
              </h3>
              <ul className="space-y-1 text-sm text-blue-700">
                <li>• Higher token limits for more conversations</li>
                <li>• More chatbots and document capacity</li>
                <li>• Priority support for faster resolution</li>
                <li>• Advanced features like API access and custom branding</li>
                <li>• Longer analytics retention for better insights</li>
              </ul>
            </CardContent>
          </Card>

          {/* Action Buttons */}
          <div className="flex justify-end space-x-3 pt-4 border-t">
            <Button variant="outline" onClick={onClose}>
              Maybe Later
            </Button>
            {selectedPlan && (
              <Button
                onClick={() => handleUpgrade(selectedPlan)}
                disabled={isLoading}
                className="bg-blue-600 hover:bg-blue-700"
              >
                {isLoading ? "Processing..." : "Upgrade Now"}
              </Button>
            )}
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default UpgradeModal;
